<template>
  <div>
    <navPatient/>
    <div class="container bg-light">
      <h1 class="text-center text-dark p-5">
        <b>Internaciones de {{patients.nombre}} {{patients.apellido}}</b>
      </h1>
      <div v-for="(value,key) in patients.internaciones" :key="key">
        <fieldset class="container">
          <b-row class="p-3">
            <b-col class="shadow-sm">
              <ul>
                <legend><h4>Internación</h4></legend>
                <hr>
                <p>
                  <b>
                    Internacion#:
                    <span>{{ key+1 }}</span>
                  </b>
                </p>
                <li>
                  <p>
                    <b>Estado: </b>
                    <span
                      v-if="value.estadoInternacion.descripcion==='En progreso' ? true : false"
                      class="text-success"
                    >{{ value.estadoInternacion.descripcion }}</span>
                    <span v-else class="text-danger">{{ value.estadoInternacion.descripcion }}</span>
                  </p>
                </li>
                <li>
                  <p>
                    <b>Motivo: </b>
                    <span>{{ value.motivo }}</span>
                  </p>
                </li>
                <li>
                  <p>
                    <b>Fecha de Ingreso:</b>
                    {{convertDateToString(value.fechaInternacion)}}
                  </p>
                </li>
              </ul>
            </b-col>
            <b-col class="shadow-sm">
              <ul>
                <legend><h4>Habitación</h4></legend>
                <hr>
                <li>
                  <p>
                    <b>Piso: </b>
                    <span>{{ value.habitacion.piso.descripcion }}</span>
                  </p>
                </li>
                <li>
                  <p>
                    <b>Tipo: </b>
                    <span>{{ value.habitacion.tipoHabitacion.descripcion }}</span>
                  </p>
                </li>
                <li>
                  <p>
                    <b>Género: </b>
                    <span>{{ value.habitacion.genero.descripcion }}</span>
                  </p>
                </li>
                <li>
                  <p>
                    <b>Número de Habitación: </b>
                    <span>{{ value.habitacion.numero }}</span>
                  </p>
                </li>
              </ul>
            </b-col>
          </b-row>
        </fieldset>
      </div>
    </div>
    <Footer/>
  </div>
</template>
<script>
import { URL } from "@/variables.js";
import axios from "axios";
import navPatient from "@/components/patient/navPatient.vue";
import Footer from "@/components/Footer.vue";
export default {
  name: "internacionesDePaciente",
  data() {
    return {
      id: this.$route.params.id,
      patients: []
    };
  },
  methods: {
    async findById() {
      axios
        .get(`${URL}/api/paciente/${this.id}`)
        .then(response => (this.patients = response.data))
        .catch(error => console.log(error));
    },
    convertDateToString(date) {
      let dateObject = new Date(date);
      let formatter = new Intl.DateTimeFormat("es", {
        weekday: "long",
        year: "numeric",
        month: "long",
        day: "numeric",
        hour: "numeric",
        minute: "numeric",
        second: "numeric"
      });
      return formatter.format(dateObject);
    }
  },
  components: {
    navPatient,
    Footer
  },
  mounted() {
    this.findById();
  }
};
</script>

